export default {
	data() {
		return {
			   //输入的内容
		    inpcont : "",
			   //历史搜索
			history : []
		}
	},
	onShow() {
		if(uni.getStorageSync('history') == ""){
			
		}else{
			this.history = uni.getStorageSync('history');
		}
	    
	},
	methods: {
		   // 返回首页
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		   
		   
		inp : function(e){
			this.inpcont = e.detail.value;
		},
		
		find : function(){
			if(this.inpcont == ""){
			   uni.showToast({
			   	 title : "搜索不可以为空！",
				 icon : "none"
			   })
			}else{
				uni.navigateTo({
					url:'findEnd/findEnd?name='+this.inpcont
				})
				// 往搜索历史里插入内容
				this.history.splice(20)
			   this.history.push(this.inpcont);
			   uni.setStorageSync('history',this.history);
			   this.inpcont = "";
			}
			
		},
		   // find函数结束
		
		
		
		clearhisr : function(){
			var that = this;
			uni.showModal({
			        title: '请选择',
			        content: '是否要清空历史记录',
			        cancelText:'清空',
			        confirmText:'取消',
			        success(res){
			          if(res.cancel){
						  that.history = [];
						  uni.showToast({
						  	title: '已清空历史记录',
							icon : "success"
						  });
						  uni.clearStorageSync("history");
			             console.log("点击了确定");
			          }else if(res.confirm){
			             console.log("点击了取消");
			          }
			        }
			      });
		},
		  // clearhisr函数结束
		
	}
}
